@media screen and (max-width: 1199px) {
    .template-title{
        margin-bottom: 48px;
        max-width: 570px;
       
        h1,
        h2 {
            font-size: 64px;
            margin-bottom: 48px;
        }
    
        &.template-title_row, 
        &.template-title_row-reverse
         {
            max-width: 767px;
            gap: 56px;

    
            h1,
            h2,
            h6 {
                min-width: 330px;
                margin-bottom: 0;
            } 
        }
    
        &.template-title_row-reverse {
            flex-direction: row-reverse;
        }
    }

    .advantages {
        gap: 24px;
        
        .label{
            top: -12px;
            left: -12px;
        }
    }

    
    
    .footer-nav {
        width: 700px;
    }


}
